import { Avatar, Divider, Stack, Typography } from '@material-ui/core';
import { Link, useRouteMatch } from 'react-router-dom';
import StyledLink, { IStyledLinkLinkProps } from '../StyledLink';

import ContactIcons from '../Contact/ContactIcons';
import React from 'react';
import StyledButton from '../StyledButton';
import StyledNavLink from '../StyledNavLink';
import StyledTypography from '../StyledTypography';
import useBreakpoints from '../../hooks/useBreakpoints';

const { PUBLIC_URL } = process.env; // set automatically from package.json:homepage

const ExternalLink = (props: IStyledLinkLinkProps) => {
  return (
    <StyledLink
      {...props}
      target="_blank"
      typographyProps={{
        variant: 'subtitle2',
      }}
    />
  );
};

const LearnMoreLink = ({ to }: { to: string }) => {
  return (
    <>
      <StyledNavLink exact to={to} label="Learn More">
        <StyledButton variant="text">
          <StyledTypography
            fontVariant="titleLight"
            capitalized={true}
            bold={true}>
            Learn More
          </StyledTypography>
        </StyledButton>
      </StyledNavLink>
    </>
  );
};

const SideBar = () => {
  const resumePageRoute = useRouteMatch(`/resume`);
  const { scaleFactor } = useBreakpoints();
  const { md } = useBreakpoints();
  return (
    <>
      <Stack spacing={6 / scaleFactor}>
        <Stack spacing={3 / scaleFactor} alignItems={md ? 'center' : undefined}>
          <Link to="/">
            <Avatar
              src={`${PUBLIC_URL}/images/me.jpg`}
              sx={{
                width: 160,
                height: 160,
              }}>
              N M
            </Avatar>
          </Link>
          <StyledTypography fontVariant="title" capitalized={true}>
            Nilesh Moradiya
          </StyledTypography>
          <StyledLink
            to="mailto:nileshmoradiya_coder@outlook.com"
            bold={true}
            capitalized={true}
            label="nileshmoradiya_coder@outlook.com"
            typographyProps={{
              variant: 'overline',
              color: 'text.disabled',
              letterSpacing: '0.15em',
            }}
          />
        </Stack>
        <Divider />
        <Stack spacing={3 / scaleFactor}>
          <StyledTypography
            variant="button"
            fontVariant="titleLight"
            bold={true}
            capitalized={true}>
            About
          </StyledTypography>
          <Typography variant="subtitle2" paragraph>
            Hi, I&apos;m Nilesh. I like building things. I am a{' '}
            <ExternalLink
              to="https://svitvasad.ac.in/"
              label="SVIT, Vasad - GTU"
            />{' '}
            graduate, and currently working as a Technical Architect/Software
            Engineer at <ExternalLink to="https://imriel.com/" label="IMRIEL" />
            . Before IMRIEL I was at{' '}
            <ExternalLink to="https://www.techgrains.com/" label="TechGrains" />
            , and <ExternalLink to="https://promactinfo.com/" label="PROMACT" />
            .
          </Typography>
          <LearnMoreLink to={resumePageRoute?.isExact ? '/about' : '/resume'} />
        </Stack>
        <Divider />
        <Stack direction="row">
          <ContactIcons />
        </Stack>
        <Typography
          display="inline"
          variant="overline"
          sx={{
            color: 'text.disabled',
          }}>
          &copy; Nilesh Moradiya{' '}
          <StyledNavLink
            to="/"
            label="nileshmoradiya.com"
            typographyProps={{
              variant: 'overline',
            }}
          />
          .
        </Typography>
      </Stack>
    </>
  );
};

export default SideBar;
